  @media (max-width: 1023px) {
    .site-nav {
      position: absolute;
      right: 0;
      left: 0;
      z-index: 1;
      min-width: 320px;
      padding-top: 100px;
      height: 100%;
      background-color: #ffffff;
      border-bottom: none; }
      .site-nav--closed {
        background-color: transparent; }
      .site-nav--closed .site-nav__list {
        display: none; }
      .site-nav--closed .site-nav__toggle {
        position: absolute;
        top: 0;
        right: 0;
        display: block;
        width: 50px;
        height: 50px;
        background-color: #0ad9c6;
        border: 0; }
      .site-nav--closed .site-nav__toggle::before {
        content: "";
        position: absolute;
        top: 11px;
        right: 10px;
        width: 30px;
        height: 30px;
        background: url("../img/burger.svg") no-repeat; }
      .site-nav--opened .site-nav__toggle {
        position: absolute;
        top: 0;
        right: 0;
        display: block;
        width: 50px;
        height: 52px;
        background-color: transparent;
        border: 0;
        z-index: 2; }
      .site-nav--opened .site-nav__toggle::before {
        content: "";
        position: absolute;
        top: 14px;
        right: 17px;
        width: 24px;
        height: 25px;
        background: url("../img/close.svg") no-repeat; } }

.site-nav__list {
